@import url(https://fonts.googleapis.com/css2?family=Domine&family=Jomolhari&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background-color: #c6b8ab;
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* + h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-bottom: 0;
  font-family: "Domine", serif;
  font-weight: normal;
}

address {
  font-style: normal;
}

address a {
  color: #333;
}

@media screen and (max-width: 800px) {
  address {
    font-size: 0.7em;
  }
}

.style_container__Z-FsV {
  margin-bottom: 30px;
  width: 100%;
}

.style_container__Z-FsV h2 {
  color: #c6b8ab;
  margin-bottom: 0.5em;
}

.style_container__Z-FsV h3 {
  margin-top: 0.5em;
}

.style_entries__zU72E {
  display: grid;
  grid-template-columns: 5fr 1fr 1fr 1fr;
  grid-column-gap: 15px;
}

@media screen and (max-width: 800px) {
  .style_container__Z-FsV {
    font-size: 0.6em;
  }
}
@media screen and (min-width: 1200px) {
  .style_container__Z-FsV:nth-child(2n + 2) {
    margin-left: 30px;
  }

  .style_container__Z-FsV {
    width: calc(50% - 15px);
  }
}

@media screen and (min-width: 1800px) {
  .style_container__Z-FsV:nth-child(2n + 2) {
    margin-left: unset;
  }
  .style_container__Z-FsV:nth-child(3n + 2),
  .style_container__Z-FsV:nth-child(3n + 3) {
    margin-left: 30px;
  }

  .style_container__Z-FsV {
    width: calc(33% - 15px);
  }
}

